import React from "react"
import foto1 from "../Archivos/Imagenes/stars.png"
import foto2 from "../Archivos/Imagenes/home-pumpkin.png"
import foto3 from "../Archivos/Imagenes/home-tree1.png"
import foto4 from "../Archivos/Imagenes/home-tree2.png"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";



export const Fiesta= () =>{
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
        <section  className="home section" id="fiesta">
           
           <div className="shape__small"></div>
            <div className="shape__big"></div>
            <div data-aos="fade-right"  className="home__container container grid">
                <div className="home__data">
                   <h1 className="home__title"><span>HALLOWEEN FEST</span> 2022 EDITION
                   <img src={foto1} alt="home image" className="home__title-img-1"></img>
                   <img src={foto1} alt="home image" className="home__title-img-2"></img></h1>
                <p className="home__description">Este Halloween te invito a que te mames responsablemente en mi morada!</p>
                </div>
                <img src={foto2} alt="home image" className="home__img"></img>
                <img src={foto4} alt="home image" className="home__tree-2"></img>
                <img src={foto3} alt="home image" className="home__tree-1"></img>
                
            </div>
        </section>
    )
}

export default Fiesta;