import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import React from "react";

export const Formulario = () =>{
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);

       const scrollUp  = () =>{
        const scrollUp = document.getElementById('scroll-up')
        this.scrollY >= 350 ? scrollUp.classList.add('showscroll')
                              :scrollUp.classList.remove('show-scroll')
       }


    return(
        <section className="home section" id="form">
           
        <div className="shape__small"></div>
        <div className="shape__big"></div>
        <div className="home__container container grid">
        <h2 className="section__title">Formulario</h2>
        <form action="https://formsubmit.co/robervgg@gmail.com"  method="POST">
    
    <input type="text" name="nombre" placeholder="Nombre" required />
    <input type="text" name="apellido" placeholder="Apellido " required />
    
    <input type="text" name="email" placeholder="Email" required />
    <textarea
      name="mensaje"
      placeholder="Escriba si viene solo o con invitados, de ser asi anote nombre y apellido de los invitados"
      required
    ></textarea>
    <input type="submit" value="ENVIAR" id="botonf" />
  </form>
        </div>
        <a href="#nvb" className="scrollup" id="scroll-up"><i class="fa-solid fa-up-long"></i></a>
    </section>
    )


}


export default Formulario;