import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import React from "react";

import foto1 from "../Archivos/Imagenes/foto1.jpeg"
import foto2 from "../Archivos/Imagenes/foto2.jpeg"
import foto3 from "../Archivos/Imagenes/foto3.jpeg"
import foto4 from "../Archivos/Imagenes/foto4.jpeg"
import foto5 from "../Archivos/Imagenes/foto5.jpeg"
import foto6 from "../Archivos/Imagenes/foto6.jpeg"
import foto7 from "../Archivos/Imagenes/foto7.jpeg"
import foto8 from "../Archivos/Imagenes/foto8.jpeg"
import foto9 from "../Archivos/Imagenes/foto9.jpeg"
import foto10 from "../Archivos/Imagenes/foto10.jpeg"
import { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-cards";


// import required modules
import { EffectCards } from "swiper";


export const Anterior= () =>{
  useEffect(() => {
    Aos.init({duration: 2000});   
   }, []);
    return(
        <section data-aos="fade" className="home section" id="#info">
           <h2 className="section__title">Fiesta del ao pasado</h2>
           <div className="shape__small"></div>
            <div className="shape__big"></div>
            <div className="home__container container grid">
            <>
      <Swiper
        effect={"cards"}
        grabCursor={true}
        modules={[EffectCards]}
        className="mySwiper"
      ><SwiperSlide><img src={foto7}></img></SwiperSlide>
        <SwiperSlide><img src={foto1}></img></SwiperSlide>
        <SwiperSlide><img src={foto2}></img></SwiperSlide>
        <SwiperSlide><img src={foto3}></img></SwiperSlide>
        <SwiperSlide><img src={foto4}></img></SwiperSlide>
        <SwiperSlide><img src={foto5}></img></SwiperSlide>
        <SwiperSlide><img src={foto6}></img></SwiperSlide>
        <SwiperSlide><img src={foto8}></img></SwiperSlide>
        <SwiperSlide><img src={foto9}></img></SwiperSlide>
        <SwiperSlide><img src={foto10}></img></SwiperSlide>
      </Swiper>
    </>
            </div>
        </section>
    )
}

export default Anterior;