import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import React from "react";

export const Informacion= () =>{
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
        <section className="home section" id="info">
           
           <div  className="shape__small"></div>
            <div  className="shape__big"></div>
            <div data-aos="fade" className="home__container container grid">
                <div className="home__data">
                <h2 className="section__title">Informacion importante</h2>
                 <ul>
                    <li >
                        <p><i class="fa-solid fa-location-pin"></i> Se envia post Inscripcin</p>
                    </li>
                    <li>
                        <p><i class="fa-solid fa-calendar-days"></i> 5 de Noviembre</p>
                    </li>
                    <li>
                        <p><i class="fa-solid fa-clock"></i> 00:00 - Hasta el amanecer</p>
                    </li>
                    <li>
                        <p></p>
                    </li>
                 </ul>
                </div>
            </div>
        </section>
    )
}

export default Informacion;