import React from "react";
import {Container} from "react-bootstrap"
import BurgerButton from "./BurgerButton";
import {useState} from "react"

import foto from "../Archivos/Imagenes/carita2.png"
import foto2 from "../Archivos/Imagenes/nav-bat.png"
export const NavBar =()=> {
    const  [clicked, setClicked] = useState(false)
    const handleClick = () =>{
        setClicked(!clicked)
    }
    return(
        <>
            <Container id="nvb" className="containerr">
                <div className="logotext">
            <img src={foto} alt="logo" className="perrito"></img>
                <h2> Athena Halloween Party</h2></div>
                <div className={`navlink ${clicked ? 'active' : ''} `}>
                    <a onClick={handleClick} href="#fiesta">Fiesta</a>
                    <a onClick={handleClick} href="#info">Informacin</a>
                    <a onClick={handleClick} href="#form">Formulario</a>
                </div>
                <div className="burger">
                <BurgerButton clicked={clicked} handleClick={handleClick}/>
                </div>
                <div className={`initial ${clicked ? 'active' : ''}`}> <img src={foto2} className="fotonav active"></img></div>
            </Container>
        </>
    )
}

export default NavBar;


