import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import React from "react";

import disfraz from "../Archivos/Imagenes/disfraz.png"
import alcohol from "../Archivos/Imagenes/alcohol.png"
import hielo from "../Archivos/Imagenes/hielo.png"
export const Cosas= () =>{
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
        <section data-aos="fade-up" className="category section">
           <h2 className="section__title">QUE TENGO QUE LLEVAR?</h2>
           <div className="shape__small"></div>
            <div className="shape__big"></div>
            <div className="category__container container grid">
                <div className="category__card">
               <img src={disfraz} alt="category image" className="category__img"></img>

               <h3 className="category__title">Disfraz</h3>
               <p classname="category__description">Lo mas importante de todo y obligatorio, venir disfrazado</p>
                 
                </div>
                <div className="category__card">
               <img src={alcohol} alt="category image" className="category__img"></img>

               <h3 className="category__title">Alcohol</h3>
               <p classname="category__description">Traete tu bebida alcoholica o analcohlica que vayas a ingerir</p>
                 
                </div>
                <div className="category__card">
               <img src={hielo} alt="category image" className="category__img"></img>

               <h3 className="category__title">Hielo</h3>
               <p classname="category__description">Si podes traer hielo genial, asi no tomamos pichi de Athena!</p>
                 
                </div>
            </div>
        </section>
    )
}

export default Cosas;